
import {
  modifierVariants,
  textInputAlignments,
  textInputVariants,
  variants,
} from "@/utility/css-helper";
import { computed, defineComponent } from "vue";
import { formatHandler, formatHandlerWrapper } from "@/helpers/generalHelper";
export default defineComponent({
  name: "TTextInput",
  props: {
    variant: {
      type: String,
      default: "primary",
      validator: (propValue: string) => {
        return !!variants[propValue];
      },
    },
    inputType: {
      type: String,
      default: "primary",
      validator: (propValue: string) => {
        return !!textInputVariants[propValue];
      },
    },
    label: {
      required: false,
      type: String,
      default: "",
    },
    leftIcon: {
      required: false,
      type: String,
      default: "",
    },
    rightIcon: {
      required: false,
      type: String,
      default: "",
    },
    rounded: {
      type: Boolean,
      required: false,
      default: false,
    },
    outline: {
      type: Boolean,
      required: false,
      default: false,
    },
    align: {
      type: String,
      required: false,
      default: "right",
      validator: (propValue: string) => {
        return !!textInputAlignments[propValue];
      },
    },
    modelValue: {
      type: String,
      required: true,
    },
    modelModifiers: {
      default: () => ({} as any),
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  computed: {
    isRight(): boolean {
      return this.align === textInputAlignments.right;
    },
    isLeft(): boolean {
      return this.align === textInputAlignments.left;
    },
    isCenter(): boolean {
      return this.align === textInputAlignments.center;
    },
  },
  setup(props, { slots }) {
    const rightPadding = computed(
      (): boolean => !!(slots.rightSlot || props.rightIcon)
    );
    const leftPadding = computed(
      (): boolean => !!(slots.leftSlot || props.leftIcon)
    );

    const variantClasses = computed((): string => {
      return (
        (!props.outline
          ? "hover:opacity-80 transition text-white disabled:opacity-50 "
          : "transition hover:opacity-80 disabled:opacity-50 ") +
        (!props.outline
          ? `bg-${props.variant} text-input-placehoder-white`
          : `bg-white text-input-placehoder-black text-dark hover:bg-${props.variant}-50 border-4 border-${props.variant} focus:border-${props.variant}-500 focus:border-${props.variant}-500`)
      );
    });

    return { rightPadding, leftPadding, variantClasses };
  },
  watch: {
    modelValue: {
      immediate: true,
      handler(value: string) {
        this.emitHandler(value, true);
      },
    },
  },
  methods: {
    emitHandler(value: string, watchCommitted = false) {
      const [formatFounded, args] = formatHandlerWrapper(
        modifierVariants.format,
        this.modelModifiers
      );

      if (formatFounded) {
        value = formatHandler(value, {
          separator: args[0],
          digitLength: args[1],
        });
        watchCommitted && this.$emit("update:modelValue", value);
      }

      !watchCommitted && this.$emit("update:modelValue", value);
    },
  },
});
